.workCoin{
    background-color: black;
}

.projectBox{
    height:auto;
    width:auto;
    background-color: white;
    margin: 3rem 3rem;
    border-radius: .3rem;
    box-shadow: .3rem .2rem .3rem .2rem gray;

}

.boxCoin{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    white-space: nowrap;
    
}

.boxCoin img{
    height: 50%;
    width: 100%;
    border-radius: .4rem;
    
}


.boxCoin img:hover{
   transform: scale(1.3);
}


.projectBox h1{
    font-size: 1.6rem;
    color: rgb(133, 19, 214);
    font-family: cursive;
    text-align: center;
}

.projectBox h2{
    font-size: 1.3rem;
    overflow: hidden;
    margin-left: 1rem;
    white-space: nowrap;
   
}

.projectBox a{
    font-size: medium;
    margin: 1rem 3rem;
}