
@media (max-width:427px) {
   
    .btn{
        width: 11rem;
      
        font-size: .9rem;
    }
    .about-img{
        width: 100vw ;
        height: auto;
        margin-left: 40%;
    }
}

.nameCoin{
    width:50%
}
.aboutCoin{
    background-color: aliceblue;
    height: auto;
    width: 100vw;

}


.aboutBox{
    width: 90vw;
    height: auto;
    padding-bottom: 6rem;
    display: grid;
    margin-left: 5vw;
    box-shadow: .2rem .2rem .3rem .4rem rgb(127, 124, 124);
   
}

.aboutBox h1{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 600;
}



.row1Coin{
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
}
.row1Coin h1{
font-size: 1.3rem;
margin-left: 1rem;
white-space: nowrap;
}

.row2 h2{
    font-size: 1rem;
    font-weight: 400;
margin: 1rem 1rem;
}

.row4{
    margin-top: 2rem;
gap: 1rem; 
 
}

.row4 button{
    margin:.2rem .1rem;
    padding: .1rem .5rem;
    font-weight: 700;
}




